import React from 'react'
import { useFetchGifs } from '../hooks/useFetchGifs';
import { GifGridItem } from './GifGridItem';

export const GifGrid = ({ category }) => {

    const { data:images , loading } = useFetchGifs( category );
    
/*     const [images, setImages] = useState([]);

    useEffect( () => {
        getGifs( category )
        //.then( images => setImages( images )). este código es igual que el de la siguiente línea, solo que en la siguiente línea está simplificado, al ser solo un argumento el que se envía, se lo puede obviar y tambien se puede obviar enviar el argumento cuando se llama a la función
            .then( setImages );
    }, [ category ]) */


    return (
        <>
            <h3>{ category }</h3>

            { loading && <p>Loading</p> }
            
            <div className="card-grid">
            {
                images.map( img => (
                    <GifGridItem
                        key = { img.id }
                        { ...img }  
                    />
                ))
            }
            </div>
        </>
    )
}
